.Banner {
  background: var(--secondary-blue-turquoise);
}

.Banner--inner {
  padding: 1em 0.5em;
  text-align: center;
}

.Banner__title {
  font-size: 35px;
  padding: 0 0.5em;
}

@media screen and (min-width: 720px) {
  .Banner--inner {
    padding: 2.5em 15%;
  }
  .Banner__title {
    padding: 0 20%;
  }
}
