
.Articles {
  position: relative;
  padding: 1em 0.5em;
}
.Articles-info {
  padding: 0 0 2em;
}
.Articles-info__title {
  background: #fff;
  color: var(--brand-blue-pelorous);
  display: inline-block;
  font-size: 18px;
  margin: 1em 0;
  padding: 5px 1em;
  text-transform: uppercase;
}
.Articles-info__list {}
.Articles-info__list-item {
  list-style-type: georgian;
  margin: 0;
}
.Articles--inner {
  overflow: auto;
}
.Articles__item {
  margin: 1em 0 3em;
}
.Articles__item:nth-child(even) {
  flex-direction: row-reverse;
}
.Articles__media {}
.Articles__excerpt {}
.Articles__title {}
.Articles__title-heading {}
.Articles__text {}

iframe {
  width: 100%;
}
@media screen and (min-width: 720px) {
  .Articles {
    padding: 2.5em 15%;
  }
  .Articles__item {
    align-items: center;
    display: flex;
    justify-content: space-between;
  }
  .Articles__media {
    width: 60%;
  }
  .Articles__excerpt {
    width: 40%;
  }
}
